@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  width: 100%;
}

.container {
  justify-content: space-between;
  align-items: center;
  margin-inline: 50px;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
}

.navbar {
  height: 60px;
  display: flex;
}

.nav-list {
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list-item .item {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav-list-item .item:hover {
  color: #00acdc;
}

.nav-list-item .is-active {
  color: #00acdc;
  margin-left: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
